const Projects = () => {
  return (
    <div className={Styles.projects} >
      
          <p className={Styles.sft} id="#projects">
            MY PROJECTS
          </p>
          <Swiper
          
        pagination={{
          type: "progressbar",
        }}
        navigation={true}
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
      
        <SwiperSlide>
          <Project item={projects[0]}/>
        </SwiperSlide>
        <SwiperSlide>
        <Project item={projects[1]}/>
        </SwiperSlide>
        <SwiperSlide>
        <Project item={projects[2]}/>
        </SwiperSlide>
        <SwiperSlide>
        <Project item={projects[3]}/>
        </SwiperSlide>
      </Swiper>
     <div>
      <p className={Styles.tU}>Github Calender</p>
      <Calendar/>
     </div>

    </div>
  )
}

export default Projects
