import React from 'react'
import Styles from "./skills.module.css"
const Skills = () => {
  return (
    <div id="#skills">
      <p className={Styles.sft}>SOFT SKILLS</p>
      <div className={Styles.skills}>
        <div className={Styles.skill}>
          <img src="data:image/png;base64," alt="teamwork"/>
          <h3>TEAMWORK</h3>
        </div>
        <div className={Styles.skill}>
          <img src="data:image/jpeg;base64," alt="Hard work"/>
          <h3>HARD-WORKING</h3>
        </div>
        <div className={Styles.skill}>
          <img src="data:image/png;base64," alt="punctual"/>
          <h3>PUNCTUAL</h3>
        </div>
        <div className={Styles.skill}>
          <img src="data:image/png;base64," alt="Problem Solving"/>
          <h3>PROBLEM-SOLVING</h3>
        </div>
      </div>
    </div>
  )
}

export default Skills

